import { faker } from '@faker-js/faker';
import { sample, sampleSize } from 'lodash';


export const COLORS = ['#FFC0CB', '#1890FF', '#00AB55', '#000000', '#FFFFFF', '#94D82D', '#FF4842', '#FFC107'];
export const CATEGORY = ['Men Runing', 'Men Sportswear', 'Men Runing', 'Men Training'];
export const STATUS = ['Published', 'Archived', 'Draft']
export const PRODUCT_STATUS = ['sale', 'new']
export const PRODUCTS = [...Array(20)].map((_, index) => ({
  id: faker.datatype.uuid(),
  name: faker.commerce.product(),
  displayName: faker.commerce.productName(),
  category: sample(CATEGORY),
  description: faker.commerce.productDescription(),
  media: faker.image.sports(),
  price: faker.datatype.number({ min: 4, max: 99, precision: 0.01 }),
  priceSale: index % 3 ? null : faker.datatype.number({ min: 19, max: 29, precision: 0.01 }),
  colors: sampleSize(COLORS, 3),

  productStatus: sample(PRODUCT_STATUS),
  status: sample(STATUS),
  limits: sample(['Members Only', '', '']),
  // status: sample(['draft', 'published' ]),
  createdAt: faker.date.past(),
  stocks: [...Array(Math.round(Math.random() * 10) + 1)].map((_, index) => (
    {
      id: faker.datatype.uuid(),
      name: faker.commerce.productName(),
      avatarUrl: faker.image.sports(),
      sku: faker.finance.bic(),
      stock: faker.finance.amount(2, 10, 0),
      price: faker.datatype.number({ min: 4, max: 99, precision: 0.01 }),
      createdAt: faker.date.past(),
    }
  )),
}));
