import PropTypes from 'prop-types';
// @mui
import { Box, Card, Link, Typography, Stack, IconButton } from '@mui/material';
import { styled } from '@mui/material/styles';
// utils
import { fCurrency, fShortenNumber } from 'utils/formatNumber';
// components
import Label from 'components/label';
import { ColorPreview } from 'components/color-utils';
import { useTheme } from '@emotion/react';
import { FavoriteBorder } from '@mui/icons-material';

// ----------------------------------------------------------------------

const StyledProductImg = styled('img')({
  top: 0,
  width: '100%',
  height: '100%',
  objectFit: 'cover',
  position: 'absolute',
});

// ----------------------------------------------------------------------

ShopProductCard.propTypes = {
  product: PropTypes.object,
};

export default function ShopProductCard({ product }) {
  const theme = useTheme();
  const { name, category, media, price, priceSale, colors, limits, status } = product;

  return (
    <Card>
      <Box sx={{ pt: '100%', position: 'relative' }}>
        <Stack direction={`row`} sx={{
          zIndex: 9,
          top: 16,
          right: 16,
          position: 'absolute',
          textTransform: 'uppercase',
        }}>
          {status && (
            <Label
              variant="filled"
              color={(status === 'sale' && 'error') || 'info'}
            >
              {status}
            </Label>
          )}

          <IconButton aria-label="delete" size="large">
            <FavoriteBorder fontSize="inherit" />
          </IconButton>
        </Stack>
        <StyledProductImg alt={name} src={media} />
      </Box>

      <Box
        sx={{
          position: 'relative',
        }}>
        <Stack
          direction="row"
          alignItems="center" j
          ustifyContent="space-between"
          sx={{
            position: 'absolute',
            top: -40,
            left: 25,
          }}>
          <Typography
            variant="h5"
            sx={{
              display: 'inline',
              bgcolor: theme.palette.grey[100],
              borderRadius: `${theme.shape.borderRadius}px`
            }}
          >
            <Typography
              component="span"
              variant="h5"
              sx={{
                color: 'text.disabled',
                textDecoration: 'line-through',
              }}
            >
              {priceSale && fCurrency(priceSale)}
            </Typography>
            &nbsp;
            {fCurrency(price)}
          </Typography>
          {priceSale && <Typography
            component="span"
            variant="h5"
            sx={{
              ml: 2,
              borderRadius: `${theme.shape.borderRadius}px`,
              color: theme.palette.error.main,
              bgcolor: theme.palette.grey[100]
            }}
          >
            {fShortenNumber(priceSale / price * 100)}%
          </Typography>}
        </Stack>

        <Stack spacing={2} sx={{ p: 3 }}>
          <ColorPreview colors={colors} />
          <Link color="inherit" underline="hover">
            <Typography variant="subtitle1" noWrap>
              {name}
            </Typography>
          </Link>
          <Typography variant="subtitle2" noWrap color="grey">
            {category}
          </Typography>

          <Typography variant="subtitle2" noWrap color="info">
            {limits}
          </Typography>
        </Stack>
      </Box>
    </Card>
  );
}
